<template>
	<div class="discussion-container">
		<div class="discussion-header">
			<div class="discussion-taolun">讨论</div>
		</div>
		<div class="discussion-content">
			<div v-for="item in discussion_list">
				<div class="discussion-headline">
					<div class="discussion-shang">
						{{item.discussion_title}}
					</div>
					<div class="response-tan">
						<img src="../assets/sangedian.png" @click="go_report(item.discussion_id,1,item.discussion_title)">
					</div>
				</div>
				<div class="discussion-bj" @click="go_response(item.discussion_id)">
					<div class="discussion-wenz">{{item.discussion_content}}</div>
					<!--<div class="discussion-around">
						<div class="discussion-tu"  v-for="one in item.img_list" @click="openSimpleDialog(one)">
							<img :src="baseUrl+one">
							<img src="../assets/img_tl1.png">
							<img :src="!!item.discussion_pic?baseUrl+item.discussion_pic:'../assets/img_tl1.png'">
						</div>
					</div>-->
					<div class="discussion-wap">
						<div class="discussion-around" v-for="one in item.img_list">
							<!--<div class="discussion-tupian" @click="openSimpleDialog(one)">-->
							<div class="discussion-tupian">
								<img :src="baseUrl+one">
							</div>
						</div>
					</div>
					<div class="discussion-between" @click="go_response(item.discussion_id)">
						<div class="discussion-flex">
							<div class="discussion-niming" v-if="item.discussion_anonymity==0">匿名</div>
							<div class="discussion-niming" v-else-if="item.discussion_anonymity==1">{{item.discussion_user_id}}</div>
							
							<div class="discussion-shijian">{{$root.formatDate(item.create_time)}}</div>
						</div>
						<div class="discussion-flex">
							<div class="discussion-xiaoxi">
								<img src="../assets/icon_pinglun.png">
							</div>
							<div class="discussion-shuliang">{{item.discussion_count}}</div>
						</div>
					</div>
				</div>
			</div>
			<div class="discussion-fahao">
				<img src="../assets/bi.png" @click="go_question()">
			</div>
		</div>
		<mu-container>
			<mu-dialog :open.sync="openSimple">
			  	<div class="discussion-tantu" @click="openBotttomSheet">
			  		<img :src="!!img_url?baseUrl+img_url:'../assets/img_tl1.png'">
			  	</div>
			</mu-dialog>
			<!--<mu-bottom-sheet :open.sync="open">
			    <mu-list @item-click="closeBottomSheet">
			      <mu-sub-header>
			      	<div class="showtu-baocun" @click="save(img_url)">点击保存</div>
			      </mu-sub-header>
			    </mu-list>
		  	</mu-bottom-sheet>-->
		</mu-container>
		<div class="discussion-footer">
			<mu-bottom-nav>
				<mu-bottom-nav-item title="首页" icon=" " to="home" tag="span"></mu-bottom-nav-item>
				<mu-bottom-nav-item title="话术" icon=" " to="verbal" tag="span"></mu-bottom-nav-item>
				<mu-bottom-nav-item title="讨论" icon=" " to="discussion" tag="span"></mu-bottom-nav-item>
				<mu-bottom-nav-item title="我的" icon=" " to="mine" tag="span"></mu-bottom-nav-item>
			</mu-bottom-nav>
		</div>
	</div>
</template>

<script>
	import Qs from 'qs'
	export default {
		name: 'home',
		data() {
			return {
				msg: '',
				data: [],
				title: '',
				user_info: {},
				discussion_list: [],
				openSimple: false,
				img_url: '',
				discussion_pic: [],
				open: false
			}
		},
		methods: {
			go_question() {
				this.$router.push('/question')
			},
			go_response(id) {
				this.$router.push('/response/' + id)
			},
			openSimpleDialog (one) {
				this.img_url = one;
		      	this.openSimple = true;
		    },
//		    openSimpleDialog (one) {
//				let that = this
//				that.img_url = one;
//				setTimeout(function(){ 
//					that.openSimple = true;
//				}, 3000);
//				
//		    },
		    closeBottomSheet () {
		      this.open = false;
		    },
		    openBotttomSheet () {
		      this.open = true;
		    },
		    go_report(discussion_id,shu,title){
				this.$router.push({
					path:'/report',
					query:{
						type:discussion_id,
						typeone:shu,
						typetwo: title
					}
				})
			},
			discussion() {
				var that = this;
				var params = {
					userid: that.user_info.user_id,
					title: that.title
					//					content: that.content,
					//					anonymity:that.anonymity,
				};
				that.$http.post('/discussion/sel_discussion', Qs.stringify(params), {

				}).then((res) => {
					if(res.meta.code == 200) {
						that.discussion_list = res.data;

						that.discussion_list = that.discussion_list.reverse();

						that.discussion_list.forEach(function(item){ //循环处理每一条数据
							item.img_list = []; //在每一条数据里面 定义一个 空的 数组  用来下面放图片路径
							if (!!item.discussion_pic) { //判断 discussion_pic 字段有值 才去处理  要不然会报错
								if (item.discussion_pic.indexOf(',') >=0) { 
									//判断discussion_pic字段里面有没有，如果没有，那就只有一条数据 不用去裁取
									item.img_list = item.discussion_pic.split(',');
								}else{
									// 只有一条数据 直接插入到img_list里面
									item.img_list.push(item.discussion_pic);
								}
							}

						})
						// that.discussion_pic = res.data.discussion_pic.split(',');
					}
				})
			},
	//			保存图片
//			save(img_url){
//				var that = this
//				dsBridge.call('saveImg',that.baseUrl+img_url);
//			}
		},
		created() {
			// 页面刚加载的时候执行
			this.discussion();
			if(!!localStorage.getItem('user_info')) {
				this.user_info = JSON.parse(localStorage.getItem('user_info'));
			}
		},
		mounted() {
			// 页面刚加载完 dom 也加载完 的时候执行

		}
	}
</script>

<style scoped>
	.discussion-container {
		width: 100%;
		height: 100%;
		/*background: #FFFFFF;*/
	}
	
	.discussion-header {
		height: 50px;
		background: #FFFFFF;
		line-height: 50px;
		border-bottom: 1px solid #eaeaea;
	}
	
	.discussion-taolun {
		font-size: 1.8rem;
		color: #000000;
		text-align: center;
		font-weight: 550;
	}
	
	.discussion-content {
		width: 100%;
		height: calc(100% - 105px);
		overflow-x: hidden;
		overflow-y: auto;
	}
	.discussion-headline{
		display: flex;
		justify-content: space-between;
		background: #FFFFFF;
		border-bottom: 1px solid #EAEAEA;
	}
	.discussion-shang {
		font-size: 1.7rem;
		/*font-weight: 600;*/
		color: #000000;
		background: #FFFFFF;
		padding: 0.5rem 0 0.5rem 1.5rem;
	}
	.response-tan img{
		width: 0.25rem;
		height: 0.8rem;
		margin-right: 1.5rem;
		margin-top: 1rem;
	}
	.discussion-bj {
		background: #FFFFFF;
		margin-bottom: 1rem;
	}
	
	.discussion-wenz {
		font-size: 1.4rem;
		color: #8e8c8c;
		padding: 0.3rem 1rem 1.2rem 1.5rem;
		/*text-indent: 2rem;*/
		text-align: left;
	}
	
	/*.discussion-around {
		display: flex;
		justify-content: space-around;
	}
	.discussion-tu{
		width: 30%;
		margin: 0 auto;
		display: flex;
		flex-wrap: wrap;
	}
	.discussion-tu img {
		width: 100%;
		height: 10rem;
		margin-bottom: 1rem;
		margin-right: 0.5rem;
	}*/
	
	.discussion-between {
		display: flex;
		justify-content: space-between;
	}
	
	.discussion-flex {
		display: flex;
	}
	
	.discussion-niming {
		font-size: 1.2rem;
		color: #dcdcdc;
		padding-left: 1.5rem;
		padding-right: 1rem;
	}
	
	.discussion-shijian {
		font-size: 1.2rem;
		color: #dcdcdc;
	}
	
	.discussion-xiaoxi img {
		width: auto;
		height: 1.2rem;
	}
	
	.discussion-shuliang {
		font-size: 1.2rem;
		color: #dcdcdc;
		padding: 0 1rem 1.1rem 1rem;
	}
	
	.discussion-fahao {
		/*display: flex;
		flex-direction: row-reverse;*/
		position: absolute;
		bottom: 5rem;
		right: 3.5rem;
	}
	
	.discussion-fahao img {
		width: auto;
		height: 4.5rem;
		margin-right: 0.5rem;
	}
	
	.discussion-footer {
		height: 55px;
		box-sizing: border-box;
		overflow: hidden;
		position: fixed;
		bottom: 0;
		width: 100%;
		background: #fff;
		border-top: 1px solid #f0f0f0;
	}
	
	/deep/ .mu-tabs-inverse {
		background-color: #FFFFFF;
	}
	
	/deep/ .mu-tab-wrapper {
		padding: 0;
	}
	
	/deep/ .mu-bottom-item-icon {
		margin-top: 6px;
		background-repeat: no-repeat;
		width: 20px;
		height: 20px;
		background-size: 100% 100%;
	}
	/*/deep/ .mu-bottom-item-text {
		color: #999999
	}*/
	/*/deep/ .mu-bottom-item-active .mu-bottom-item-text {
		color: #229efe;
	}*/
	
	/deep/ .mu-bottom-item-active {
		color: rgba(0, 0, 0, .54);
	}
	
	/deep/ .mu-bottom-item:nth-child(1) .mu-bottom-item-icon {
		background-image: url(../assets/tab_shouye1.png);
	}
	
	/deep/ .mu-bottom-item:nth-child(2) .mu-bottom-item-icon {
		background-image: url(../assets/tab_huashu1.png);
	}
	
	/deep/ .mu-bottom-item:nth-child(3) .mu-bottom-item-icon {
		background-image: url(../assets/huashu-hongse.png);
	}
	
	/deep/ .mu-bottom-item:nth-child(4) .mu-bottom-item-icon {
		background-image: url(../assets/tab_wode1.png);
	}
	
	/deep/ .mu-bottom-item:nth-child(3) .mu-bottom-item-text {
		color: #f683a2;
		font-size: 14px;
	}
	
	/deep/ .mu-bottom-item:nth-child(1) .mu-bottom-item-text {
		font-size: 10px;
	}
	
	/deep/ .mu-bottom-item {
		min-width: 0px;
	}
	/*/deep/ .mu-bottom-item-text {
		color: #000000
	}
	
	/deep/ .mu-bottom-item-active .mu-bottom-item-text {
		color: #8CADF5;
	}*/
	.discussion-tantu{
		margin-bottom: -1rem;
	}
	.discussion-tantu img{
		width: 100%;
		height: auto;
		margin: 0 auto;
	}
	/deep/ .mu-dialog-body{
		padding: 0;
		width: 100%;
		margin: 0 auto;
		/*display: flex;
		justify-content: center;*/
	}
	/deep/ .mu-dialog{
		-webkit-box-shadow: none;
		box-shadow: none;
	}
	.discussion-wap{
		display: flex;
		flex-wrap: wrap;
		/*justify-content: center;*/
	}
	.discussion-around  {
		display: flex;
		justify-content: space-around;
		width: 33.33%;
		/*padding-top: 1rem;*/
		padding-bottom: 1rem;
	}
	/*.discussion-tupian{
		display: flex;
		justify-content: center;
	}*/
	.discussion-tupian img{
		width: 100%;
		height: 10rem;
		padding: 0 0.5rem;
		border-radius: 15px;
	}
</style>


